.NotesList {
    margin-top: 30px;
    margin-left: 20px;
    text-align: left;
}
.NotesList button:first-child {
    margin-right: 15px;
}
.noteEditor textarea {
    resize: none;
}
.Modal {
    position: absolute;
    top: 39%;
    left: 50%;
    /* right: 70%;
    bottom: auto; */
    width: 300px;
    margin-right: -50%;
    border: 1px solid rgb(204, 204, 204);
    background: rgb(255, 255, 255);
    border-radius: 4px;
    outline: none;
    padding: 20px;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.modalHeader {
    margin-bottom: 15px;
    text-align: center;
}
.modalDescription input {
    margin-bottom: 40px;
}
.modalFooter button:first-child {
    margin-right: 15px;
}
.nodesBlock {
    margin-top: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.listGroup {
    width: 30%;
    min-width: 300px;
    margin-right: 50px;
}
.listGroup button {
    overflow: hidden;
}
.noteEditor {
    width: 30%;
    min-width: 300px;
    margin-bottom: 40px;
}



/* MEDIA QUERIES */
@media all and (max-width: 670px) {
    .listGroup {
        width: 90%;
        min-width: 0;
        margin-right: 0;
    }
    .noteEditor {
        width: 90%;
        min-width: 0;
    }
}



